import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import ButtonGroup from "@mui/material/ButtonGroup";

const dispatchedEvents = [];

const SrsMuiButtonGroup = function (props, ref) {
  return (
    <ButtonGroup
      variant={props.variant}
      color={props.color}
      size={props.size}
      disabled={props.disabled}
      fullWidth={props.fullWidth}
      aria-label={props["aria-label"]}
      theme={props.muiTheme}
    >
      <slot />
    </ButtonGroup>
  );
};

const styles = `
  /* Custom styles for the MuiButtonGroup component */
`;

const props = [
  {
    name: "variant",
    defaultValue: "outlined",
    type: "string",
  },
  {
    name: "color",
    defaultValue: "default",
    type: "string",
  },
  {
    name: "size",
    defaultValue: "medium",
    type: "string",
  },
  {
    name: "disabled",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "fullWidth",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "aria-label",
    defaultValue: "",
    type: "string",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
  // Add other props specific to ButtonGroup if needed
];

const slots = [
  {
    name: "default",
    allowed: ["srs-mui-button"],
  },
];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiButtonGroup),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
